import VueRouter from 'vue-router'
import FormSignin from '../components/account/Login.vue'
import service from '../service/service'
const router = new VueRouter({
  mode: "history",
  routes:[
    {
      path:'/', name:'default', component: FormSignin
    },
    {
      path:'/login', name:'login' , component: FormSignin
    },
    {
      path:'/register', name:'register', 
      component: () => import(/* webpackChunkName:"register" */ '../components/account/Register.vue'),
    },
    {
      path:'/home',
      name: 'home', 
      component: () => import('../view/Home.vue'),
      meta:{
        requiredAuth: true
      }
    },
    {
      path: '/notes', name: 'notes', component: () => import('../view/Notes.vue'),
      meta:{
        requiredAuth: true
      }
    },
    {
      path: '*',
      name: "notFound",
      component: () => import('../view/NoteFound.vue')
    }

  ],
        
    
  })
router.beforeEach((to, from, next) =>{
  if(to.meta.requiredAuth){
    if(!service.auth()){
      next({name: 'default'})
    } else {
      service.authAsync().then(res =>{
        if(res['isAuth']){
          next()
        } else next({name: 'default'})
      })
    }
  } else {
    if(service.auth()){
      service.authAsync().then(res =>{
        if(res['isAuth']){
          next({name : 'home'})
        } else next()
      })
    } else next()
  } 
})
export default router