<template>
  <v-app>
    <v-row justify="center" align="center">
      <v-col cols="12" sm="9" md="7" lg="5">
        <v-card elevation="2" class="rounded-lg">
          <v-form ref="form" lazy-validation>
          <v-card-title class="text-h4 font-weight-bold">Login to note</v-card-title>
          <v-divider class="mx-4"></v-divider>
          <v-card-text>
            <v-text-field
                v-model="userLogin.username"
                :rules="[() => !!userLogin.username || 'Username is required']"
                label="Username"
                required
            ></v-text-field>
            <v-text-field
                v-model="userLogin.password"
                label="Password"
                :rules="[() => !!userLogin.password || 'Password is required']"
                :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                :type="show ? 'text' : 'password'"
                name="password"
                @click:append="show = !show"
                class="input-group--focused"
            ></v-text-field>  
          </v-card-text>
          <v-divider class="mx-4"></v-divider>
          <v-card-actions class="pa-4">
            <v-btn class="rounded-lg font-weight-bold" color="warning" @click="submit" block>Login</v-btn>
          </v-card-actions>
          <v-card-text class="font-italic text-center pt-0">Don't have an account? <router-link to="/register">Register</router-link> now</v-card-text>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
  </v-app>
</template>

<script>
import service from '../../service/service'
export default {
  name: 'FormSignin',
  data(){
      return{ 
        userLogin:{
            username: '',
            password: '',
        },
        show: false
      };
    
 },
    methods:{
        submit: function(e){
            e.preventDefault();
            if(this.validate()){
                service.login(this.userLogin)
                   .then(response => {
                       if(!response['isLogin']) this.$emit("snackbar", response['message'], 2)
                       else{
                           this.$router.push('/home')
                           this.$emit("snackbar", response['message'], 1)
                       } 
                    }) 
            }
        },
        validate(){
        return this.$refs.form.validate();
      }
    }
}

</script>

