import axios from "axios";

const configApi = 'https://notecloneios.herokuapp.com/'
var userObj = {
    lastName: '',
    firstName: '',
    age: '',
    gender: '',
};
var allNote = null;
var TOKEN = '';
function login(userLogin){
    return axios
        .post(configApi + "api/v1/auth", userLogin)
        .then(response => response = response.data)
        .then(response => {
            if(response['token'] == '') {
                return {
                    isLogin: false,
                    message: response['message']
                }
            }
            else{
                localStorage.setItem('token', response['token'])
                return {
                    isLogin: true,
                    message: response['message']
                }
            } 
        }) 
        .catch(() => {
            return {
                isLogin: false,
                message: '404 ERR_CONNECTION_REFUSED'
            }
        });
}
async function authAsync(){
    let token = localStorage.getItem('token');
    let result = await axios
        .get(configApi + "api/v1/" + token)
        .then(response => response = response.data)
        .then(response => {
            if(response != ''){
                TOKEN = token;
                saveUser(response);
                return {
                    isAuth: true
                }
            } else{
                localStorage.removeItem('token');
                return {
                    isAuth: false
                }
            }
        })
        .catch(() =>{
            localStorage.removeItem('token');
            return {
                isAuth: false
            }
        });
    return result;  
}
function auth(){
    if(localStorage.getItem('token')) return true;
    return false;
}
function register(userRegister){
    return axios
        .post(configApi +"api/v1/register", userRegister)
        .then(response => response = response.data)
        .then(response => {
            if(response['token'] == '') {
                return {
                    isRegister: false,
                    message: response['message']
                }
            }
            else{
                localStorage.setItem('token', response['token'])
                return {
                    isRegister: true,
                    message: response['message']
                }
            } 
        }) 
        .catch(() => {
            return {
                isLogin: false,
                message: '404 ERR_CONNECTION_REFUSED'
            }
        });
}
function saveUser(user){
    userObj.lastName = user.lastName;
    userObj.firstName = user.firstName;
    userObj.age = user.age;
    userObj.gender = user.gender
}
function getUser(){
    return userObj;
}
async function loadAllNote(){
    let token = localStorage.getItem('token');
    let result = await axios
        .get(configApi + 'api/v1/notes/' + token)
        .then(response => response = response.data)
        .then(response => {
            allNote = response
            return true;
        })
        .catch((error) => {
            console.log(error)
        })
    return result;
}
async function getAllNotesAsync(){
    const isTrue = await loadAllNote();
    if(isTrue) return allNote
}
function logOut(){
    localStorage.removeItem('token');
    if(!localStorage.getItem('token')){
        return true;
    }
    return false;
}
async function addNote(newNote){
    let isTrue = await axios
        .post(configApi + 'api/v1/notes/' + TOKEN, newNote)
        .then(res => {
            if(res.status == 200){
                return true;
            }
         })
    if(isTrue) return getAllNotesAsync();
}
async function setNote(note){
    let isTrue = await axios
        .put(configApi + 'api/v1/notes/' + TOKEN + '/' + note['id'], note)
        .then(res => {
            if(res.status == 200){
                return true;
            }
        })
    if(isTrue) return getAllNotesAsync();
}
async function deleteNote(id){
    let isTrue = await axios
        .delete(configApi + 'api/v1/notes/' + TOKEN + '/' + id)
        .then(res => {
            if(res.status == 200){
                return true;
            }
        })
    if(isTrue) return getAllNotesAsync();
}
export default{
    login,
    authAsync,
    register,
    auth,
    getUser,
    getAllNotesAsync,
    logOut,
    addNote,
    setNote,
    deleteNote,
}