import Vue from 'vue'
import router from './router/index'
import VueRouter from 'vue-router' 
import Vuetify from "vuetify";
import vuetify from "./plugins/vuetify";
import App from './App.vue'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faSearch, faTrashAlt, faEdit, faAngleLeft, faSave, faTimesCircle } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faSearch, faTrashAlt, faEdit, faAngleLeft, faSave, faTimesCircle)

Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.config.productionTip = false
Vue.use(VueRouter, Vuetify)
new Vue({
  render: h => h(App),
  router,
  vuetify
}).$mount("#app");



