<template>
  <div>
    <v-app>
      <v-snackbar v-model="snackbarModel.showing" :timeout="snackbarModel.timeout" :multi-line="true" :color="snackbarModel.color" absolute top right text>
        <v-layout align-center pr-4>
            <v-icon class="pr-3" :color="snackbarModel.icon.color" dark large>{{snackbarModel.icon.name}}</v-icon>
            <v-layout column>
                <div>
                    <strong>{{snackbarModel.message.title}}</strong>
                </div>
                <div>{{snackbarModel.message.text}}</div>
            </v-layout>
        </v-layout>
      </v-snackbar>
      <transition name="fade">
        <router-view v-on:snackbar="handleSnackbar"/>
      </transition>
    </v-app>
  </div>
</template>

<script>
export default{
  name: 'App',
  data: function(){
    return{
      isAuth: false,
      token: null,
      snackbarModel: {
        showing: false,
        message: {
          title: '',
          text: ''
        },
        color: '',
        timeout: -1,
        icon: {
          name:'', 
          color: '',
        },
      }
      
    }
  },
  methods:{
    handleSnackbar(value, check){
      switch(check){
        case 1: 
          // Login Successfuly
          this.snackbarModel.message.title = 'Login successfully!';
          this.snackbarModel.message.text = value;
          this.snackbarModel.showing = true;
          this.snackbarModel.timeout = 3000;
          this.snackbarModel.color='success';
          this.snackbarModel.icon.name='mdi-check-circle';
          this.snackbarModel.icon.color="green"
          break;
        case 2:
          // Login Error
          this.snackbarModel.message.title = 'Login failed!';
          this.snackbarModel.message.text = value;
          this.snackbarModel.showing = true;
          this.snackbarModel.timeout = 3000;
          this.snackbarModel.color='error';
          this.snackbarModel.icon.name='mdi-alert-octagon-outline';
          this.snackbarModel.icon.color="error"
          break;
        case 3:
          // Resgister Error
          this.snackbarModel.message.title = 'Resgister failer!';
          this.snackbarModel.message.text = value;
          this.snackbarModel.showing = true;
          this.snackbarModel.timeout = 3000;
          this.snackbarModel.color='error';
          this.snackbarModel.icon.name='mdi-alert-octagon-outline';
          this.snackbarModel.icon.color="error"
          break;
      }
    }
  }
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  overflow:hidden;
  position: relative;
  height: 100%;
}
/* body{
  background: black;
  height: 100%;
  margin: 0;
  max-height: 100vh;
}
*/
html{
  overflow: hidden;
}
a{
  color: white !important
}
a:hover{
  color: #ff9900 !important;
}
.fade-enter-active,
.fade-leave-active{
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave{
  opacity: 0;
}

</style>